<template>
  <div class="text-center mt-3 ">
    <span class="fs-1 fw-bold text-white bg-brown opacity-75 px-5"
      >最新消息</span
    >
  </div>
  <div class="container my-3 ">
    <div class="row mt-2 border shadow">
      <div class="col-md-6">
        <img
        :src=article.image
          class="img-fluid"
        />
      </div>
      <div class="col-md-6 ">
        <h4 class="mt-4">{{ article.title }}</h4>
        <p class="text-danger fs-4">
          活動時間 : {{ article.description }}
        </p>
        <p class="text-muted">
          活動內容 : {{ article.content }}
        </p>
      </div>
    </div>
  </div>
  <div class="text-center mt-3 ">
    <span class="fs-1 fw-bold text-white bg-brown opacity-75 px-5"
      >熱門商品</span
    >
  </div>
  <AllProduct />
</template>
<script>
import emitter from '@/methods/emitter';
import AllProduct from '@/components/AllProduct.vue';
export default {
  components:{
    AllProduct,
  },
  inject: ["emitter"],
  data() {
    return {
      article: {},
      articleId:'',
    };
  },
  methods: {
    getArticles() {
      this.articleId = this.$route.params.articleId;
      // console.log(this.articleId);
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/article/${this.articleId}`;
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          // console.log(res);
          this.article = res.data.article
        }
      });
    },
  },
  created() {
    this.getArticles();
  }
};
</script>
